#contact-cont{
    position: relative;
    top: -30vh;
    margin: auto auto;
    left: 0;
    right: 0;
    height: 55vh;
    width: 50vw;
    border: 2px solid yellow;
    text-align: justify;
    background-color: #2f394d;
}
#flex-cont{
    display: flex;
    justify-content: space-evenly;
    height: 80%;
}
.contact-el{
    color:white;
    display: flex;
    flex-direction: column;
    justify-content: start;
    /* border: 2px solid red; */
    max-width: 40%;
    margin: 5% auto;
    margin-bottom: 0;
    flex-wrap: wrap;

}
.contact-el h6{
    min-height: 80px;
}
#icons{
    display: flex;
    flex-direction: row;
    justify-content: center;
    
}

#icons li{
    margin: 5%;
}
#icons a{
    all:unset;
}
#email-text{
    margin-bottom: 0;

}